<template lang="pug">
  
  .settings-wallpaper
    .wallpapers-list
      .wallpapers-list__item(
        v-for='(item, index) in wallpapersList' 
        @click='selectWallpaper(index)'
      )
        img(:src='require(`@/assets/img/wallpapers/${item}`)')
        .wallpapers-list__selected(v-if='selectedWallpaper === index')
          svg
            use(xlink:href='#success')

</template>

<script>
  
  import {mapState} from 'vuex'

  export default {
    computed: mapState({
      wallpapersList: state => state.settings.wallpapersList,
      selectedWallpaper: state => state.settings.selectedWallpaper
    }),
    methods: {
      selectWallpaper(i) {
        if (i == this.selectedWallpaper) return
        this.$store.commit('SELECT_WALLPAPER', i)
        this.$alert.show('Обои установлены')
      }
    }
  }

</script>

<style lang="sass"> @import '../../assets/sass/phone/apps/settings/wallpapers' </style>